import React from 'react';
import { JiraIssue } from '../../types/jira.types';
import { getStatusColor, getPriorityColor } from '../../utils/calculations';
import { formatRelativeTime } from '../../utils/dateHelpers';

interface RecentActivityProps {
  issues: JiraIssue[];
  loading?: boolean;
}

const RecentActivity: React.FC<RecentActivityProps> = ({ issues, loading = false }) => {
  if (loading) {
    return (
      <div className="card p-6">
        <h3 className="text-lg font-semibold mb-4">Atividade Recente</h3>
        <div className="space-y-3">
          {[...Array(5)].map((_, index) => (
            <div key={index} className="skeleton h-16 w-full"></div>
          ))}
        </div>
      </div>
    );
  }

  if (!issues || issues.length === 0) {
    return (
      <div className="card p-6">
        <h3 className="text-lg font-semibold mb-4">Atividade Recente</h3>
        <div className="flex items-center justify-center h-32 text-gray-500">
          <p>Nenhuma atividade recente</p>
        </div>
      </div>
    );
  }

  const getStatusBadge = (status: string) => {
    const color = getStatusColor(status);
    return (
      <span 
        className={`badge text-xs px-2 py-1`}
        style={{ backgroundColor: `${color}20`, color: color }}
      >
        {status}
      </span>
    );
  };

  const getPriorityBadge = (priority: string) => {
    const color = getPriorityColor(priority);
    return (
      <span 
        className={`badge text-xs px-2 py-1`}
        style={{ backgroundColor: `${color}20`, color: color }}
      >
        {priority}
      </span>
    );
  };

  const getTypeIcon = (type: string) => {
    const icons: Record<string, string> = {
      'Story': '📖',
      'Task': '📋',
      'Bug': '🐛',
      'Epic': '🎯',
      'Incident': '🚨'
    };
    return icons[type] || '📄';
  };

  return (
    <div className="card p-6">
      <div className="flex items-center justify-between mb-4">
        <h3 className="text-lg font-semibold">Atividade Recente</h3>
        <span className="text-sm text-gray-500">
          {issues.length} issues
        </span>
      </div>
      
      <div className="space-y-3 max-h-96 overflow-y-auto">
        {issues.slice(0, 10).map((issue) => (
          <div 
            key={issue.id} 
            className="flex items-start gap-3 p-3 border border-gray-200 rounded-lg hover:bg-gray-50 transition-colors"
          >
            <div className="flex-shrink-0 mt-1">
              <span className="text-lg">
                {getTypeIcon(issue.fields.issuetype.name)}
              </span>
            </div>
            
            <div className="flex-1 min-w-0">
              <div className="flex items-start justify-between gap-2">
                <div className="flex-1 min-w-0">
                  <h4 className="text-sm font-medium text-gray-900 truncate">
                    {issue.key}
                  </h4>
                  <p className="text-sm text-gray-600 truncate mt-1">
                    {issue.fields.summary}
                  </p>
                </div>
                <div className="flex-shrink-0 text-xs text-gray-500">
                  {formatRelativeTime(issue.fields.updated)}
                </div>
              </div>
              
              <div className="flex items-center gap-2 mt-2">
                {getStatusBadge(issue.fields.status.name)}
                {getPriorityBadge(issue.fields.priority.name)}
                {issue.fields.assignee && (
                  <span className="text-xs text-gray-500">
                    👤 {issue.fields.assignee.displayName}
                  </span>
                )}
              </div>
            </div>
          </div>
        ))}
      </div>
      
      {issues.length > 10 && (
        <div className="mt-4 text-center">
          <button className="text-sm text-primary hover:text-blue-700 font-medium">
            Ver todas as {issues.length} issues
          </button>
        </div>
      )}
    </div>
  );
};

export default RecentActivity;
