import { useState, useEffect, useCallback } from 'react';
import { JiraIssue, SprintData, DashboardData, FilterState } from '../types/jira.types';
import { jiraApi } from '../services/jiraApi';
import { DataTransformService } from '../services/dataTransform';

interface UseJiraDataReturn {
  data: DashboardData | null;
  loading: boolean;
  error: string | null;
  refetch: () => Promise<void>;
  isConnected: boolean;
}

export const useJiraData = (filters?: FilterState): UseJiraDataReturn => {
  const [data, setData] = useState<DashboardData | null>(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [isConnected, setIsConnected] = useState(false);

  const fetchData = useCallback(async () => {
    try {
      setLoading(true);
      setError(null);

      // Test connection first
      const connected = await jiraApi.testConnection();
      setIsConnected(connected);

      if (!connected) {
        throw new Error('Não foi possível conectar ao Jira. Verifique suas credenciais.');
      }

      // Fetch all data in parallel
      const [issues, sprints, boards, projects] = await Promise.all([
        jiraApi.getAllIssues(),
        jiraApi.getBoards().then(async (boardList) => {
          const allSprints: SprintData[] = [];
          for (const board of boardList.slice(0, 5)) { // Limit to first 5 boards
            try {
              const boardSprints = await jiraApi.getSprints(board.id);
              allSprints.push(...boardSprints);
            } catch (err) {
              console.warn(`Failed to fetch sprints for board ${board.id}:`, err);
            }
          }
          return allSprints;
        }),
        jiraApi.getBoards(),
        jiraApi.getProjects()
      ]);

      // Apply filters if provided
      let filteredIssues = issues;
      if (filters) {
        filteredIssues = DataTransformService.filterIssues(issues, {
          projects: filters.projects,
          statuses: filters.statuses,
          types: filters.issueTypes,
          assignees: filters.assignees,
          priorities: filters.priorities,
          dateRange: filters.dateRange
        });
      }

      // Transform data
      const metrics = DataTransformService.getMetricsCards(filteredIssues, sprints);
      const dashboardData: DashboardData = {
        issues: filteredIssues,
        sprints,
        boards,
        projects,
        metrics: {
          totalOpen: metrics[0].value as number,
          totalClosed: metrics[1].value as number,
          completionRate: parseInt(metrics[2].value as string),
          averageResolutionTime: metrics[3].value as number,
          currentVelocity: metrics[4].value as number,
          overdueIssues: metrics[5].value as number
        }
      };

      setData(dashboardData);
    } catch (err) {
      const errorMessage = err instanceof Error ? err.message : 'Erro desconhecido ao buscar dados';
      setError(errorMessage);
      console.error('Error fetching Jira data:', err);
    } finally {
      setLoading(false);
    }
  }, [filters]);

  const refetch = useCallback(async () => {
    await fetchData();
  }, [fetchData]);

  useEffect(() => {
    fetchData();
  }, [fetchData]);

  return {
    data,
    loading,
    error,
    refetch,
    isConnected
  };
};

export const useJiraConnection = () => {
  const [isConnected, setIsConnected] = useState(false);
  const [isChecking, setIsChecking] = useState(false);

  const checkConnection = useCallback(async () => {
    setIsChecking(true);
    try {
      const connected = await jiraApi.testConnection();
      setIsConnected(connected);
      return connected;
    } catch (error) {
      console.error('Connection check failed:', error);
      setIsConnected(false);
      return false;
    } finally {
      setIsChecking(false);
    }
  }, []);

  useEffect(() => {
    checkConnection();
  }, [checkConnection]);

  return {
    isConnected,
    isChecking,
    checkConnection
  };
};

export const useSprintData = (sprintId?: number) => {
  const [sprintIssues, setSprintIssues] = useState<JiraIssue[]>([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);

  const fetchSprintIssues = useCallback(async (id: number) => {
    try {
      setLoading(true);
      setError(null);
      const issues = await jiraApi.getSprintIssues(id);
      setSprintIssues(issues);
    } catch (err) {
      const errorMessage = err instanceof Error ? err.message : 'Erro ao buscar issues da sprint';
      setError(errorMessage);
      console.error('Error fetching sprint issues:', err);
    } finally {
      setLoading(false);
    }
  }, []);

  useEffect(() => {
    if (sprintId) {
      fetchSprintIssues(sprintId);
    }
  }, [sprintId, fetchSprintIssues]);

  return {
    sprintIssues,
    loading,
    error,
    refetch: () => sprintId && fetchSprintIssues(sprintId)
  };
};

export default useJiraData;
