import React, { useState, useEffect } from 'react';
import { useJiraData } from '../../hooks/useJiraData';
import { useFilters, useFilterOptions } from '../../hooks/useFilters';
import { DataTransformService } from '../../services/dataTransform';
import MetricsCard from './MetricsCard';
import SprintProgress from './SprintProgress';
import IssuesByStatus from './IssuesByStatus';
import TeamVelocity from './TeamVelocity';
import BurndownChart from './BurndownChart';
import IssuesByType from './IssuesByType';
import RecentActivity from './RecentActivity';
import FilterPanel from '../common/FilterPanel';
import LoadingSpinner from '../common/LoadingSpinner';

const Dashboard: React.FC = () => {
  const { filters, updateFilters, resetFilters, hasActiveFilters, getFilterSummary } = useFilters();
  const { data, loading, error, refetch, isConnected } = useJiraData(filters);
  const [isFilterOpen, setIsFilterOpen] = useState(false);
  const [, setLastUpdated] = useState<Date | undefined>();

  const filterOptions = useFilterOptions(data);

  useEffect(() => {
    if (data) {
      setLastUpdated(new Date());
    }
  }, [data]);

  const handleRefresh = async () => {
    await refetch();
  };

  if (!isConnected) {
    return (
      <div className="min-h-screen flex items-center justify-center bg-gray-50">
        <div className="max-w-md w-full bg-white rounded-lg shadow-lg p-6 text-center">
          <div className="w-16 h-16 bg-red-100 rounded-full flex items-center justify-center mx-auto mb-4">
            <span className="text-2xl">⚠️</span>
          </div>
          <h2 className="text-xl font-semibold text-gray-900 mb-2">
            Erro de Conexão
          </h2>
          <p className="text-gray-600 mb-4">
            Não foi possível conectar ao Jira. Verifique suas credenciais no arquivo .env
          </p>
          <button
            onClick={handleRefresh}
            className="btn btn-primary"
          >
            Tentar Novamente
          </button>
        </div>
      </div>
    );
  }

  if (error) {
    return (
      <div className="min-h-screen flex items-center justify-center bg-gray-50">
        <div className="max-w-md w-full bg-white rounded-lg shadow-lg p-6 text-center">
          <div className="w-16 h-16 bg-red-100 rounded-full flex items-center justify-center mx-auto mb-4">
            <span className="text-2xl">❌</span>
          </div>
          <h2 className="text-xl font-semibold text-gray-900 mb-2">
            Erro ao Carregar Dados
          </h2>
          <p className="text-gray-600 mb-4">{error}</p>
          <button
            onClick={handleRefresh}
            className="btn btn-primary"
          >
            Tentar Novamente
          </button>
        </div>
      </div>
    );
  }

  if (loading && !data) {
    return (
      <div className="min-h-screen flex items-center justify-center bg-gray-50">
        <LoadingSpinner size="lg" text="Carregando dados do Jira..." />
      </div>
    );
  }

  if (!data) {
    return (
      <div className="min-h-screen flex items-center justify-center bg-gray-50">
        <div className="text-center">
          <h2 className="text-xl font-semibold text-gray-900 mb-2">
            Nenhum Dado Disponível
          </h2>
          <p className="text-gray-600">
            Não foi possível carregar os dados do dashboard
          </p>
        </div>
      </div>
    );
  }

  // Transform data for charts
  const issuesByStatus = DataTransformService.getIssuesByStatus(data.issues);
  const issuesByType = DataTransformService.getIssuesByType(data.issues);
  const recentActivity = DataTransformService.getRecentActivity(data.issues);
  const activeSprint = data.sprints.find(sprint => sprint.state === 'active');
  
  // Generate velocity data
  const velocityData = DataTransformService.generateVelocityData(data.issues, data.sprints);
  
  // Generate burndown data if there's an active sprint
  const burndownData = activeSprint 
    ? DataTransformService.generateBurndownData(data.issues, activeSprint)
    : [];

  return (
    <div className="min-h-screen bg-background">
      <div className="container mx-auto px-6 py-8">
        {/* Header */}
        <div className="flex items-center justify-between mb-8">
          <div>
            <h1 className="text-3xl font-bold text-gray-900">Dashboard Jira</h1>
            <p className="text-gray-600 mt-1">
              Visão executiva dos projetos e métricas de performance
            </p>
          </div>
          
          <div className="flex items-center gap-4">
            {hasActiveFilters && (
              <div className="text-sm text-gray-600 bg-blue-50 px-3 py-1 rounded-full">
                {getFilterSummary()}
              </div>
            )}
            
            <FilterPanel
              filters={filters}
              onFiltersChange={updateFilters}
              onReset={resetFilters}
              options={{
                projects: filterOptions.projectOptions,
                sprints: filterOptions.sprintOptions,
                issueTypes: filterOptions.issueTypeOptions,
                statuses: filterOptions.statusOptions,
                assignees: filterOptions.assigneeOptions,
                priorities: filterOptions.priorityOptions
              }}
              isOpen={isFilterOpen}
              onToggle={() => setIsFilterOpen(!isFilterOpen)}
            />
          </div>
        </div>

        {/* Metrics Cards */}
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 xl:grid-cols-6 gap-6 mb-8">
          {data.metrics && Object.entries(data.metrics).map(([key, value]) => (
            <MetricsCard
              key={key}
              metric={{
                title: key.replace(/([A-Z])/g, ' $1').replace(/^./, str => str.toUpperCase()),
                value: value,
                color: key.includes('Open') || key.includes('overdue') ? 'text-danger' : 
                       key.includes('Closed') || key.includes('completion') ? 'text-success' : 'text-primary'
              }}
              loading={loading}
            />
          ))}
        </div>

        {/* Charts Grid */}
        <div className="grid grid-cols-1 lg:grid-cols-2 gap-6 mb-8">
          <IssuesByStatus data={issuesByStatus} loading={loading} />
          <IssuesByType data={issuesByType} loading={loading} />
        </div>

        {/* Sprint and Velocity */}
        <div className="grid grid-cols-1 lg:grid-cols-2 gap-6 mb-8">
          {activeSprint && (
            <SprintProgress sprint={activeSprint} loading={loading} />
          )}
          <TeamVelocity data={velocityData} loading={loading} />
        </div>

        {/* Burndown Chart */}
        {burndownData.length > 0 && (
          <div className="mb-8">
            <BurndownChart data={burndownData} loading={loading} />
          </div>
        )}

        {/* Recent Activity */}
        <div className="mb-8">
          <RecentActivity issues={recentActivity} loading={loading} />
        </div>
      </div>
    </div>
  );
};

export default Dashboard;
