import axios, { AxiosInstance, AxiosResponse } from 'axios';
import { JiraIssue, SprintData, BoardData, ProjectData, JiraApiConfig } from '../types/jira.types';

class JiraApiService {
  private api: AxiosInstance;
  private config: JiraApiConfig;

  constructor() {
    this.config = {
      domain: (import.meta as any).env.VITE_JIRA_DOMAIN || '',
      email: (import.meta as any).env.VITE_JIRA_EMAIL || '',
      apiToken: (import.meta as any).env.VITE_JIRA_API_TOKEN || '',
    };

    if (!this.config.domain || !this.config.email || !this.config.apiToken) {
      throw new Error('Jira configuration is missing. Please check your environment variables.');
    }

    this.api = axios.create({
      baseURL: `https://${this.config.domain}/rest/api/3`,
      headers: {
        'Authorization': `Basic ${btoa(`${this.config.email}:${this.config.apiToken}`)}`,
        'Content-Type': 'application/json',
        'Accept': 'application/json',
      },
    });

    // Request interceptor for logging
    this.api.interceptors.request.use(
      (config) => {
        console.log(`Making request to: ${config.url}`);
        return config;
      },
      (error) => {
        console.error('Request error:', error);
        return Promise.reject(error);
      }
    );

    // Response interceptor for error handling
    this.api.interceptors.response.use(
      (response) => response,
      (error) => {
        console.error('API Error:', error.response?.data || error.message);
        return Promise.reject(error);
      }
    );
  }

  // Test connection to Jira
  async testConnection(): Promise<boolean> {
    try {
      await this.api.get('/myself');
      return true;
    } catch (error) {
      console.error('Connection test failed:', error);
      return false;
    }
  }

  // Get all projects
  async getProjects(): Promise<ProjectData[]> {
    try {
      const response: AxiosResponse<ProjectData[]> = await this.api.get('/project');
      return response.data;
    } catch (error) {
      console.error('Error fetching projects:', error);
      throw error;
    }
  }

  // Get all boards
  async getBoards(): Promise<BoardData[]> {
    try {
      const response = await this.api.get('/agile/1.0/board');
      return response.data.values;
    } catch (error) {
      console.error('Error fetching boards:', error);
      throw error;
    }
  }

  // Get sprints for a board
  async getSprints(boardId: number): Promise<SprintData[]> {
    try {
      const response = await this.api.get(`/agile/1.0/board/${boardId}/sprint`);
      return response.data.values;
    } catch (error) {
      console.error('Error fetching sprints:', error);
      throw error;
    }
  }

  // Get issues using JQL
  async getIssues(jql: string, startAt: number = 0, maxResults: number = 100): Promise<JiraIssue[]> {
    try {
      const response = await this.api.get('/search', {
        params: {
          jql,
          startAt,
          maxResults,
          fields: [
            'summary',
            'status',
            'issuetype',
            'priority',
            'assignee',
            'created',
            'updated',
            'duedate',
            'customfield_10016',
            'labels',
            'project',
            'sprint'
          ].join(','),
        },
      });
      return response.data.issues;
    } catch (error) {
      console.error('Error fetching issues:', error);
      throw error;
    }
  }

  // Get issues for a specific sprint
  async getSprintIssues(sprintId: number): Promise<JiraIssue[]> {
    try {
      const response = await this.api.get(`/agile/1.0/sprint/${sprintId}/issue`, {
        params: {
          fields: [
            'summary',
            'status',
            'issuetype',
            'priority',
            'assignee',
            'created',
            'updated',
            'duedate',
            'customfield_10016',
            'labels',
            'project'
          ].join(','),
        },
      });
      return response.data.issues;
    } catch (error) {
      console.error('Error fetching sprint issues:', error);
      throw error;
    }
  }

  // Get all issues (with pagination)
  async getAllIssues(jql: string = 'order by updated DESC'): Promise<JiraIssue[]> {
    const allIssues: JiraIssue[] = [];
    let startAt = 0;
    const maxResults = 100;
    let hasMore = true;

    while (hasMore) {
      try {
        const issues = await this.getIssues(jql, startAt, maxResults);
        allIssues.push(...issues);
        
        if (issues.length < maxResults) {
          hasMore = false;
        } else {
          startAt += maxResults;
        }
      } catch (error) {
        console.error('Error in pagination:', error);
        hasMore = false;
      }
    }

    return allIssues;
  }

  // Common JQL queries
  getJqlQueries() {
    return {
      openIssues: 'status != Done AND status != Cancelled',
      closedIssues: 'status = Done OR status = Cancelled',
      currentMonthIssues: 'created >= startOfMonth() AND created <= endOfMonth()',
      criticalIncidents: 'type = Incident AND priority in (Highest, High) AND status != Done',
      activeSprintIssues: 'sprint in openSprints()',
      overdueIssues: 'due < now() AND status != Done',
      bugs: 'issuetype = Bug',
      stories: 'issuetype = Story',
      tasks: 'issuetype = Task',
      incidents: 'issuetype = Incident',
    };
  }
}

// Export singleton instance
export const jiraApi = new JiraApiService();
export default jiraApi;
