import React from 'react';
import { MetricCard } from '../../types/jira.types';

interface MetricsCardProps {
  metric: MetricCard;
  loading?: boolean;
}

const MetricsCard: React.FC<MetricsCardProps> = ({ metric, loading = false }) => {
  if (loading) {
    return (
      <div className="card p-6 animate-pulse">
        <div className="flex items-center justify-between mb-4">
          <div className="skeleton h-4 w-24"></div>
          <div className="skeleton h-6 w-6 rounded"></div>
        </div>
        <div className="skeleton h-8 w-16 mb-2"></div>
        <div className="skeleton h-3 w-20"></div>
      </div>
    );
  }

  const getTrendIcon = (trend?: 'up' | 'down' | 'neutral') => {
    switch (trend) {
      case 'up':
        return '📈';
      case 'down':
        return '📉';
      default:
        return '➡️';
    }
  };

  const getTrendColor = (trend?: 'up' | 'down' | 'neutral') => {
    switch (trend) {
      case 'up':
        return 'text-success';
      case 'down':
        return 'text-danger';
      default:
        return 'text-gray-500';
    }
  };

  return (
    <div className="card p-6 hover:shadow-lg transition-all duration-200">
      <div className="flex items-center justify-between mb-4">
        <h3 className="text-sm font-medium text-gray-600">{metric.title}</h3>
        <div className="text-2xl">
          {metric.icon}
        </div>
      </div>
      
      <div className="flex items-baseline justify-between">
        <div>
          <p className={`text-3xl font-bold ${metric.color || 'text-gray-900'}`}>
            {metric.value}
          </p>
          {metric.change !== undefined && (
            <div className="flex items-center mt-2">
              <span className={`text-sm ${getTrendColor(metric.trend)}`}>
                {getTrendIcon(metric.trend)} {Math.abs(metric.change)}%
              </span>
              <span className="text-xs text-gray-500 ml-2">
                vs período anterior
              </span>
            </div>
          )}
        </div>
      </div>
    </div>
  );
};

export default MetricsCard;
